.floatingDiv {
    display: flex;
    background: #fff;
    box-shadow: 0px 19px 60px rgb(0 0 0 / 8%);
    border-radius: 17px;
    align-items: center;
    width: 15rem;
    height: 5rem;
}

.floatingDiv>img {
    transform: scale(.4);
}

@media screen and (max-width: 1350px) {
    .floatingDiv {
        width: 14rem;
    }

    .floatingDiv>img {
        transform: scale(.3.5);
    }

    .floatingDiv>span {
        font-size: 12px;
    }
}